/* Navbar.css */

/* Reset styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Body styles */
  body {
    background: #eaeef6;
    font-family: 'Open Sans', sans-serif;
  }
  
  /* Navbar styles */
  .navbar {
    position: fixed;
    top: 1rem;
    left: 1rem;
    background: #fff;
    border-radius: 10px;
    padding: 1rem 0;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.03);
    height: calc(100vh - 5rem); /* Adjust height based on your layout */
  }
  
  .navbar__menu {
    position: relative;
  }
  
  .navbar__item {
    &:last-child {
      &:before {
        content: '';
        position: absolute;
        opacity: 0;
        z-index: -1;
        top: 0;
        left: 1rem;
        width: 3.5rem; /* Adjust width based on your layout */
        height: 3.5rem; /* Adjust height based on your layout */
        background: #406ff3; /* Primary color */
        border-radius: 17.5px; /* Adjust based on your layout */
        transition: 250ms cubic-bezier(1, 0.2, 0.1, 1.2) all;
      }
    }
  
    /* Gooey effect */
    @keyframes gooeyEffect {
      0% {
        transform: scale(1, 1);
      }
      50% {
        transform: scale(0.5, 1.5);
      }
      100% {
        transform: scale(1, 1);
      }
    }
  
    &:hover {
      &:last-child:before {
        opacity: 1;
      }
    }
  }
  
  .navbar__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 3.5rem; /* Adjust height based on your layout */
    width: 5.5rem; /* Adjust width based on your layout */
    color: #6a778e; /* Text color */
    transition: 250ms ease all;
  
    span {
      position: absolute;
      left: 100%;
      transform: translateX(-3rem); /* Adjust based on your layout */
      margin-left: 1rem;
      opacity: 0;
      pointer-events: none;
      color: #406ff3; /* Primary color */
      background: #fff;
      padding: 0.75rem;
      border-radius: 17.5px; /* Adjust based on your layout */
      transition: 250ms ease all;
    }
  
    &:hover {
      color: #fff;
    }
  
    .navbar:not(:hover) &:focus,
    &:hover {
      span {
        opacity: 1;
        transform: translateX(0);
      }
    }
  }
  